import gantt from "./gantt.vue";

// 
const myPlugin = {
  // eslint-disable-next-line
  version: __VERSION__,
  // install
  // Vue
  // eslint-disable-next-line
  install(Vue, options) {
    // vue'gantt'
    Vue.component("v-gantt-why", gantt);
  }
};
// 
if (typeof window !== "undefined" && window.Vue) {
  window.Vue.use(myPlugin);
}

// main.jsVue.use()
export default myPlugin;
